import React, { Component } from 'react'
import './detail.scss'
import { connect } from 'react-redux';
import { Button } from 'antd';
import Http from '../../services/http'


let toBeFixed = {
    analysis: "分析",
    answer: "答案",
    question: "题目",
    option_A: "选项A",
    option_B: "选项B",
    option_C: "选项C",
    option_D: "选项D",
    // title:"试卷名称",
}


class CustomerDetail extends Component {
    constructor(props) {
        super(props);
        const { uid } = props.match.params;
        this.uid = uid
    }

    api = {
        fixedErrorQuestion: "/api/fixedErrorQuestion"
    }

    beFixed = {}

    componentDidMount() {
        let uid = this.uid
        let { errorQuestionList, dispatch } = this.props
        let question = errorQuestionList.find(res => res.id === uid)
        if (!question) {
            console.log('返回重新获取！')
            return
        }
        dispatch({
            type: "setCurrentQuestion",
            payload: question
        })
    }

    cancel = () => {
        const history = this.props.history
        history.go(-1)
    }

    enter = async () => {
        let { currentQuestion, history } = this.props
        let { fixedErrorQuestion } = this.api
        let emptyList = ['option_C','option_D']
        let fixed = {}
        for (let key in this.beFixed) {
            let couldBeEmpty = emptyList.indexOf(key) >= 0
            if (!this.beFixed[key] && couldBeEmpty) {
                continue
            }
            fixed[key] = this.beFixed[key]
        }
        fixed.id = currentQuestion.id
        let response = await Http.post(fixedErrorQuestion, fixed)
        if (response.type) history.go(-1)
    }

    changeValue = (e, type) => {
        let { dispatch, currentQuestion } = this.props
        let value = e.target.value
        this.beFixed[type] = value
        dispatch({
            type: "setCurrentQuestion",
            payload: {
                ...currentQuestion,
                [`${type}`]: value
            }
        })
    }

    render() {
        let nameList = Object.keys(toBeFixed)
        let { currentQuestion } = this.props
        return (
            <div className="detail-page">
                <h2>修改详情</h2>
                {nameList.map(name => (
                    <div key={name} className="clearfix detail">
                        <div className="title">{toBeFixed[name]}</div>
                        <textarea value={currentQuestion[name]} onChange={e => this.changeValue(e, name)}>
                        </textarea>
                    </div>
                ))}
                <div className="clearfix button-list">
                    <Button type="primary" onClick={() => this.enter()} >确定</Button>
                    <Button onClick={() => this.cancel()}>取消</Button>
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state) => {
    return state.reducer || {}
}

export default connect(mapStateToProps)(CustomerDetail) 


